.toggle {
  width: 40px;
  height: 20px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: .604701px solid #4b48db;
  border-radius: 20px;
  display: inline-block;
  position: relative;
}

.toggle > .checkbox {
  display: none;
}

.toggle > .switch {
  cursor: pointer;
  background-color: #fff;
  transition: all .3s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.toggle > .switch:before {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #4b48db;
  transition: all .3s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

.checkbox:checked + .switch:before {
  transform: translateX(20px);
}

.checkbox:checked + .switch {
  background-color: gray;
}

.toggle.round > .switch {
  border-radius: 20px;
}

.toggle.round > .switch:before {
  border-radius: 50%;
}

